p,
ul {
  padding: 0;
  margin: 0;
}
ul {
  list-style: none;
}

.container {
  max-width: 1230px;
  padding: 0 15px;
  margin: 0 auto;
  display: grid;
  grid-template-columns: 1fr 5fr;
  grid-template-areas:
    "header header"
    "menu main";
  grid-gap: 15px;
}

.main {
  grid-area: main;
}
